import hyRequest from '@/service'

//
export const getCurrentSong = (ids: number) => {
  return hyRequest.get({
    url: '/song/detail',
    params: {
      ids
    }
  })
}

//
export const getSongLyric = (id: number) => {
  return hyRequest.get({
    url: '/lyric',
    params: {
      id
    }
  })
}
